
<script>
    export default {
        name:"LocationComponent",
        props: {
            url: String,
        },
        methods: {
        setUrl(url) {
            console.log("ËÆæÂ§á‰ΩçÁΩÆÊï∞ÊçÆ‰º†ËæìÂÖ≥Èó≠")
            if (this.socket) {
                this.socket.close()
            }
            this.socket = new WebSocket(url)
            console.log("Êñ∞ÁöÑËÆæÂ§á‰ΩçÁΩÆÊï∞ÊçÆ‰º†ËæìÂ∑≤ÂºÄÂêØ")
        },
        success(pos) {
            this.locationFound = true;
            console.log("Ëé∑ÂèñÂÆö‰ΩçÊï∞ÊçÆ")
            this.data = pos
            this.lastTime = new Date(pos.timestamp).toLocaleTimeString();
            const crd = pos.coords;
            this.latitude = crd.latitude;
            this.longitude = crd.longitude;
            this.accuracy = crd.accuracy;
            this.heading = crd.heading;
            this.speed = crd.speed;
            if (this.socket == undefined) {
                return
            }
            let payload = {
                type: "Geolocation",
                timestamp: pos.timestamp,
                latitude: pos.coords.latitude,
                longitude: pos.coords.longitude,
                altitude: pos.coords.altitude,
                accuracy: pos.coords.accuracy,
                altitudeAccuracy: pos.coords.altitudeAccuracy,
                heading: pos.coords.heading,
                speed: pos.coords.speed,
            }
            this.socket.send(JSON.stringify(payload))
        },
        error(err) {
            this.locationFound = false;
            console.warn(`ERROR(${err.code}): ${err.message}`);
        },
    },
    data() {
        return {
            data: undefined,
            socket: undefined,
            latitude: "",
            longitude: "",
            accuracy:"",
            lastTime: "",
            heading: "",
            speed:"",
            locationFound: false,
            options: {
                enableHighAccuracy: true,
                timeout: 5000,
                maximumAge: 0
            }
        }
    },
    mounted() {
        if ("geolocation" in navigator) {
            console.log("ÂºÄÂßãÂÆö‰Ωç!")
            navigator.geolocation.watchPosition(this.success, this.error, this.options)
        }
    },
}
</script>
<template>
    <div v-if="locationFound">
        <h3>üß≠ËÆæÂ§á‰ΩçÁΩÆ</h3>
        <!-- <p>{{'üåèÁªèÂ∫¶Ôºö'+data.coords.latitude}}</p>
        <p>{{'üåèÁ∫¨Â∫¶Ôºö'+longitude}}</p> -->
        <p>{{'üåèÁªèÂ∫¶Ôºö'+latitude}}</p>
        <p>{{'üåèÁ∫¨Â∫¶Ôºö'+longitude}}</p>
        <p>{{"‚¨ÜÔ∏èÊåáÂêëÔºö"+heading}}</p>
        <p>{{"üöóÈÄüÂ∫¶Ôºö"+speed}}</p>
        <p>{{'üî∫Á≤æÁ°ÆÂ∫¶Ôºö'+accuracy+'Á±≥'}}</p>
  </div>
  <h1 v-else>Êâæ‰∏çÂà∞‰ΩçÁΩÆÔºÅ</h1>
  <p>{{"‚è±Ô∏è‰∏ä‰∏ÄÊ¨°ÂÆö‰ΩçÊõ¥Êñ∞Êó∂Èó¥Ôºö"+lastTime}}</p>
</template>