<script>
export default {
    name:"DeviceMovement",
    props: {
        url: String,
    },
    data() {
        return {
            move_data: undefined,
            ori_data: undefined,
            socket: undefined,
        }

    },
    methods: {
        setUrl(url) {
            console.log("ËÆæÂ§áËøêÂä®Êï∞ÊçÆ‰º†ËæìÂÖ≥Èó≠")
            if (this.socket) {
              this.socket.close()
            }
            this.socket = new WebSocket(url)
            console.log("Êñ∞ÁöÑËÆæÂ§áËøêÂä®Êï∞ÊçÆ‰º†ËæìÂ∑≤ÂºÄÂêØ")
        },


        handleMotionEvent(event) {
            this.move_data = event;
            if (this.url != null) {
                if (this.socket == undefined) {
                    return
                }
                let payload = {
                    type: "DeviceMotion",
                    timestamp: Date.now(),
                    acceleration: {
                        x: event.acceleration.x,
                        y: event.acceleration.y,
                        z: event.acceleration.z,
                    },
                    accelerationIncludingGravity: {
                        x: event.accelerationIncludingGravity.x,
                        y: event.accelerationIncludingGravity.y,
                        z: event.accelerationIncludingGravity.z,
                    },
                    rotationRate: {
                        alpha: event.rotationRate.alpha,
                        beta: event.rotationRate.beta,
                        gamma: event.rotationRate.gamma,
                    }
                }
                this.socket.send(JSON.stringify(payload))
            }
        },
        handleOrientationEvent(event) {
            this.ori_data = event;
            if (this.url != null) {
                if (this.socket == undefined) {
                    this.socket = new WebSocket(this.url)
                }
                let payload = {
                    type: "DeviceOrientation",
                    timestamp: Date.now(),
                    alpha: event.alpha,
                    beta: event.beta,
                    gamma: event.gamma,
                }
                this.socket.send(JSON.stringify(payload))
            }
        },
        iphonePermission() {
            DeviceMotionEvent.requestPermission().then(r => {
                if (r=='granted') {
                    window.addEventListener("devicemotion", this.handleMotionEvent)
                } else {
                    alert("Êó†Ê≥ïËé∑ÂæóËÆæÂ§áËøêÂä®Êï∞ÊçÆÔºÅ")
                }
            })
            DeviceOrientationEvent.requestPermission().then(r => {
                if (r=='granted') {
                    window.addEventListener("deviceorientation", this.handleOrientationEvent)
                } else {
                    alert("Êó†Ê≥ïËé∑ÂæóËÆæÂ§áÊñπÂêëÊï∞ÊçÆÔºÅ")
                }
            })
        },
        androidPermission() {
            window.addEventListener("devicemotion", this.handleMotionEvent);
            window.addEventListener("deviceorientation", this.handleOrientationEvent);
        },
        grantPermissions() {
            if(/iPhone/i.test(navigator.userAgent)){
                this.iphonePermission()
            } else if (/Android/i.test(navigator.userAgent)) {
                this.androidPermission()
            } else {
                alert(`‰∏çÊîØÊåÅÁöÑÁöÑUserAgent: ${navigator.userAgent}`)
            }
        }
    },
}
</script>

<template>
    <button @click="grantPermissions" v-if="!move_data && !ori_data">ÂºÄÂêØËøêÂä®‰º†ÊÑüÂô®</button>
    <div v-if="move_data">
        <h3>üì±ËÆæÂ§áÁä∂ÊÄÅ</h3>
        <h4>üî∏ÂÖ≠ËΩ¥Âä†ÈÄüÂ∫¶</h4>
        <p>{{`X:${move_data.acceleration.x.toFixed(2)} Y:${move_data.acceleration.y.toFixed(2)} Z:${move_data.acceleration.z.toFixed(2)}`}}</p>
        <p>{{`Alpha:${move_data.rotationRate.alpha.toFixed(2)} Beta:${move_data.rotationRate.beta.toFixed(2)} Gamma:${move_data.rotationRate.gamma.toFixed(2)}`}}</p>
    </div>
    <div v-if="ori_data">
        <h4>üîÉËÆæÂ§áÊóãËΩ¨Ëßí(deg)</h4>
        <p>{{`Alpha:${ori_data.alpha.toFixed(2)} Beta:${ori_data.beta.toFixed(2)} Gamma:${ori_data.gamma.toFixed(2)}`}}</p>
    </div>
</template>